import jwt_decode from "jwt-decode";
import axios from "axios";

export default {
  data() {
    // 토큰가져오기
    const token = localStorage.getItem("token");
    const decoded = jwt_decode(token);
    const index = decoded.index;
    // 요청받은 인터뷰
    axios
      .get(`https://localhost:8443/sug/${index}`, {
        params: {
          index: index,
        },
      })
      .then((res) => {
        console.log(res.data);
        this.tableData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
    return {
      radio: "1",
      submitteddate: "",
      submittedtime: "",
      responseform: [{ submitteddate: "" }, { submittedtime: "" }],
      tableData: [
        // {
        //   company_id: 1,
        //   company_name: "삼성피자",
        //   company_field: "??????????",
        //   date1: "2021-08-30",
        //   time1: "13:00-14:00",
        //   date2: "2021-08-31",
        //   time2: "13:00-14:00",
